<template>
    <p v-show="!!linkHref">
        <slot></slot>
        <a :href="linkHref" target="_blank">{{linkHref}}</a>
    </p>
</template>

<script>
export default {
  props: {
    rawText: String
  },
  methods: {
    extractLink (str) {
      const res = /.*href="(.*(wikipedia|youtu).*?)".*/gim.exec(str);
      return res && res[1];
    }
  },
  computed: {
    linkHref () {
      return this.extractLink(this.rawText);
    }
  }
};
</script>

